* {
  box-sizing: border-box;
}

body {
  background-color: rgba(47, 189, 255, .42);
  font-family: cursive;
  line-height: 1.5;
}

.breed-select {
  width: 250px;
  height: 46px;
  color: #000;
  background-color: #c3cdeb;
  border: outset #a9a9a9;
  border-radius: 10px;
  margin: 0 auto;
  font-family: inherit;
  font-size: 26px;
  line-height: 30px;
  position: relative;
  overflow-y: auto;
}

h2 {
  font-size: 26px;
}

h3 {
  font-size: 20px;
}

.cat-info {
  flex-direction: row;
  justify-content: left;
  gap: 20px;
  padding-top: 40px;
  display: flex;
}

.cat-info p {
  width: 600px;
  font-size: 20px;
}

.inv {
  display: none;
}

.loader {
  width: 82px;
  height: 18px;
  position: relative;
  top: 50%;
  left: 50%;
}

.loader:before, .loader:after {
  content: "";
  width: 16px;
  height: 16px;
  background: #ff3d00;
  border-radius: 50%;
  animation: jump .5s ease-in infinite alternate;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, 10%);
}

.loader:after {
  color: #fff;
  background: none;
  animation: split .5s ease-out infinite alternate;
  top: 100%;
  box-shadow: 32px -20px, -32px -20px;
}

@keyframes split {
  0% {
    box-shadow: 8px -20px, -8px -20px;
  }

  100% {
    box-shadow: 32px -20px, -32px -20px;
  }
}

@keyframes jump {
  0% {
    transform: translate(-50%, -150%);
  }

  100% {
    transform: translate(-50%, 10%);
  }
}

